import React, { useState } from 'react';
import logo from './logo.svg';
import { QrCodePix } from 'qrcode-pix';
import './App.css';
import PIX from 'react-qrcode-pix';

function Pix() {
  const [title, setTitle] = useState('Nada');
  const [img, setImg] = useState('');
  const [phone, setPhone] = useState('phone');
  const generate = () => {
    const qrCodePix = QrCodePix({
      version: '01',
      key: '47756277000102', //or any PIX key
      name: 'Marcos Vieira da Silva',
      city: 'SAO PAULO',
      // transactionId: 'LAB FORRO', //max 25 characters
      message: phone,
      value: 1.5,
    });
    qrCodePix.base64().then((data) => {
      setImg(data);
    });
    setTitle(qrCodePix.payload());
  };

  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          Edit <code>src/App.tsx</code> and save to reload. l
        </p>
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn React
        </a>
        <input
          type="text"
          value={phone}
          onChange={(event) => setPhone(event.target.value)}
        ></input>
        <button onClick={generate}>CLICK</button>
        <img alt="adsds" src={img}></img>
        <p title={title}>{title}</p>

        <PIX
          pixkey="47756277000102"
          merchant="Lab de Forro"
          city="Sao Paulo"
          // cep="25.850-000"
          code={phone}
          amount={1}
          onLoad={generate}
          resize={384}
          image="https://github.com/WalquiriaGodoy/react-typescript-web-page-lab/raw/main/src/assets/img/logolab2.png"
          variant="fluid"
          padding={30}
          color="#357"
          bgColor="#def"
          bgRounded
          divider
        />
      </header>
    </div>
  );
}

function App() {
  const [title, setTitle] = useState('Under Construction');

  return (
    <div className="App">
      <header className="App-header">
        <p>{title}</p>
      </header>
    </div>
  );
}

export default App;
